<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-overlay :value="overlayUnclosed">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-dialog
      transition="dialog-top-transition"
      max-width="600"
      v-model="leaveRequestDialog"
    >
      <v-card>
        <v-toolbar
          color="primary"
          dark
        >About to send leave request</v-toolbar>
        <v-card-text>
          Are you sure that you want to request {{ this.days }} Days starting {{ this.$moment(this.startDate).format("DD-MM-YYYY") }} and ending {{ this.$moment(this.endDate).format("DD-MM-YYYY") }}?
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn @click="leaveRequestDialog = false" color="error">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn @click="createLeaveRequest">Yes, Proceed</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/practitioner/' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-data-table
      :headers="leaveDetailsHeaders"
      :items="leaveDetails"
      :items-per-page="15"
      class="elevation-1"
    ></v-data-table>
    <v-card>
      <v-card-title primary-title>
        Select Category*
      </v-card-title>
      <v-card-text>
        <v-row no-gutters>
          <v-col cols="3" v-for="category in leaveCategories" :key="category.value">
            <v-checkbox
              v-model="selectedCategories"
              :label="category.text"
              :value="category.value"
              :disabled="disabledCategory[category.value]"
              @change="checkCategorySelection"
            />
          </v-col>
        </v-row>
        <div v-if="invalidDates" style="color:red">
          Start Date must be before end date<br>
        </div>
        <v-form
          ref="form"
          v-model="valid"
        >
        <v-row>
          <v-col cols="6">
            <v-menu
              v-model="startDateMenu"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="startDateFormatted"
                  label="Start Date*"
                  prepend-inner-icon="mdi-calendar"
                  readonly
                  v-on="on"
                  outlined
                  hide-details="auto"
                  dense
                  @input="days = 0"
                />
              </template>
              <v-date-picker
                v-model="startDate"
                @change="startDateFormatted = formatDate(startDate)"
                @input="startDateMenu = false"
              />
            </v-menu>
          </v-col>
          <v-col cols="6">
            <v-menu
              v-model="endDateMenu"
              :close-on-content-click="false"
              :nudge-right="40"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="endDateFormatted"
                  label="End Date*"
                  prepend-inner-icon="mdi-calendar"
                  readonly
                  v-on="on"
                  outlined
                  hide-details="auto"
                  dense
                  @input="days = 0"
                />
              </template>
              <v-date-picker
                v-model="endDate"
                @change="endDateFormatted = formatDate(endDate)"
                @input="endDateMenu = false"
              />
            </v-menu>
          </v-col>
          <v-col cols="6" v-if="requiresSupportingDoc">
            <fhir-attachment
              v-if="requiresSupportingDoc"
              field="value[x]:valueAttachment"
              label="Supporting Document"
              min="1"
              max="1"
              path="attachment"
              :slotProps="{source:{path:'.extension:attachment', data: []}}"
              :edit="true"
            />
          </v-col>
          <v-col cols="6" v-if="hasSupervisee">
            <fhir-reference 
              field="value[x]:valueReference"
              :edit="true"
              path="actingSupervisor"
              displayCondition=""
              id="actingSupervisor"
              targetProfile="http://ihris.org/fhir/StructureDefinition/ihris-practitioner"
              targetResource="Practitioner"
              report="ihris-es-report-staff-directorate"
              reportReturnValue="ihris-es-report-position-list.id"
              referenceDisplayPath="Practitioner.name.given.first() + ' ' + Practitioner.name.family.first()"
              label="Acting Supervisor"
              displayType="reportSelect"
              min="1"
              max="1"
            />
          </v-col>
          <v-col v-if="days || days === 0" cols="12">
            <b>This will give you <template style="color:red">{{ days }}</template> Days of leave</b><br>
            <div style="color:red" v-if="accruedDays !== 'N/A' && accruedDays < days">
              You are requesting more days than what you have accrued, adjust the end date
            </div>
            <div style="color:red" v-if="days >= 90 && selectedCategories.includes('unpaidauthorized')">
              This will require ED Approval
            </div>
          </v-col>
        </v-row>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-btn color="success" :disabled="!canSubmitLeave" @click="displayRequestDialog">Submit Request</v-btn>
        <v-spacer></v-spacer>
        <v-btn color="primary" :disabled="!canGoNext" @click="getDays">Check</v-btn>
      </v-card-actions>
      <br>
      <br>
    </v-card>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      valid: true,
      overlay: false,
      overlayUnclosed: false,
      loading: false,
      leaveRequestDialog: false,
      leaveDetails: [],
      leaveCategories: [],
      selectedCategories: [],
      disabledCategory: {},
      startDateMenu: false,
      endDateMenu: false,
      startDateFormatted: '',
      endDateFormatted: '',
      startDate: '',
      endDate: '',
      days: '',
      balanceDays: '',
      accruedDays: '',
      errors: {
        days: []
      },
      leaveDetailsHeaders: [{
        text: "Period",
        value: "period"
      }, {
        text: "Category",
        value: "category"
      }, {
        text: "Leave Days",
        value: "maxDays"
      }, {
        text: "Accrued",
        value: "accrued"
      }, {
        text: "Days on Leave",
        value: "daysOnLeave"
      }, {
        text: "Expired",
        value: "expired"
      }, {
        text: "Balance",
        value: "balance"
      }],
      hasSupervisee: false
    }
  },
  watch: {
    endDateFormatted() {
      this.days = ""
    },
    startDateFormatted() {
      this.days = ""
    }
  },
  computed: {
    requiresSupportingDoc() {
      if(this.selectedCategories.includes('study') || this.selectedCategories.includes('sick') || this.selectedCategories.includes('sickhalfpay') || this.selectedCategories.includes('specialsick') || this.selectedCategories.includes('maternity')) {
        return true
      }
      return false
    },
    canGoNext() {
      if(this.selectedCategories.length && this.startDate && this.endDate && !this.invalidDates) {
        return true
      }
      return false
    },
    canSubmitLeave() {
      if(this.days && (this.days <= this.accruedDays || this.accruedDays === 'N/A')) {
        return true
      }
      return false
    },
    invalidDates() {
      if(this.startDate && this.endDate && this.$moment(this.startDate).isAfter(this.endDate)) {
        return true
      }
      return false
    },
    daysrule() {
      return [
        (v) => {
          this.errors.days = []
          if(!v) {
            this.errors.days = ["Days is required"]
            return "Days is required"
          }
          if(!Number.isInteger(v)) {
            this.errors.days = ["Days must be a number"]
            return "Days must be a number"
          }
          return true
        }
      ]
    }
  },
  methods: {
    populateCarry() {
      this.overlayUnclosed = true
      fetch("/site/leave/populateCarry/" + this.queries.practitioner).then(() => {
        Promise.all([this.getLeaveBalance(), this.getLeaveCategories()]).then(() => {
          this.overlayUnclosed = false
        })
      })
    },
    displayRequestDialog() {
      this.$refs.form.validate()
      if (!this.valid) return
      this.leaveRequestDialog = true
    },
    formatDate(date) {
      if (!date) return null;
      return this.$moment(date).format("DD-MM-YYYY");
    },
    getDays() {
      fetch("/site/leave/balance/" + this.queries.practitioner + "/" + this.startDate).then(response => response.json().then((response) => {
        let leave = response.find((leave) => {
          return this.selectedCategories.includes(leave.categoryid)
        })
        this.balanceDays = leave.balance
        this.accruedDays = leave.accrued
        fetch("/site/leave/getDays/" + this.startDate + "/" + this.endDate).then(response => response.json().then(async(response) => {
          this.days = response
        }))
      }))
    },
    checkCategorySelection() {
      this.disabledCategory = {}
      if(this.selectedCategories.length === 0) {
        return
      }
      for(let category of this.leaveCategories) {
        if(this.selectedCategories.includes(category.value)) {
          continue
        }
        this.$set(this.disabledCategory, category.value, true)
      }
      // if(this.selectedCategories.includes('annual') || this.selectedCategories.includes('bankedleave')) {
      //   this.$set(this.disabledCategory, 'annual', false)
      //   this.$set(this.disabledCategory, 'bankedleave', false)
      // }
    },
    getLeaveCategories() {
      return new Promise((resolve) => {
        fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/na-leave-category-profile&_count=200").then(response => response.json().then(async(categories) => {
          if(categories && categories.entry && categories.entry.length) {
            for(let entry of categories.entry) {
              let name = entry.resource.extension.find((ext) => {
                return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-basic-name"
              }).valueString
              this.leaveCategories.push({
                text: name,
                value: entry.resource.id
              })
            }
          }
          return resolve()
        }))
      })
    },
    checkSupervisee() {
      return new Promise((resolve) => {
        fetch("/site/leave/checkSupervisee/" + this.queries.practitioner).then(response => response.json().then((response) => {
          if(response.total) {
            this.hasSupervisee = true
          }
          this.overlayUnclosed = false
          return resolve()
        }))
      })
    },
    getLeaveBalance() {
      return new Promise((resolve) => {
        fetch("/site/leave/balance/" + this.queries.practitioner).then(response => response.json().then(async(response) => {
          this.leaveDetails = response
          await this.checkSupervisee()
          return resolve()
        }))
      })
    },
    createLeaveRequest() {
      this.$refs.form.validate()
      if (!this.valid) return
      this.overlayUnclosed = true;
      let values = []
      this.populateValues(this.$children, values)
      let attachment = values.find(val => val.path === 'attachment')
      let actingSupervisor = values.find(val => val.path === 'actingSupervisor')
      let body = {
        startDate: this.startDate,
        endDate: this.endDate,
        leaveCategory: this.selectedCategories
      }
      if(attachment) {
        body.attachment = attachment.value
      }
      if(actingSupervisor) {
        body.actingSupervisor = actingSupervisor.value.reference
      }
      fetch("/site/leave/createLeaveRequest/" + this.queries.practitioner, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual",
        body: JSON.stringify(body),
      }).then((response) => {
        this.overlayUnclosed = false;
        this.leaveRequestDialog = false
        if(response.status === 201) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Leave Request Created Successfully",
          });
          response.json().then((response) => {
            this.$router.push("/custom/leave-request-view?path=/namibia/components&id=" + response.id)
          })
        } else {
          response.json().then((response) => {
            this.$store.commit("setMessage", {
              type: "error",
              text: response.message
            });
          })
        }
      }).catch(() => {
        this.overlayUnclosed = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed To Create Carry Forward Request."
        });
      })
    },
    populateValues(children, values) {
      for (let child of children) {
        if(child.qField && child.value && child.path) {
          values.push({
            qField: child.qField,
            path: child.path,
            value: child.value
          })
        }
        if (child.$children) {
          this.populateValues(child.$children, values)
        }
      }
    }
  },
  components: {
    "fhir-attachment": () => import(/* webpackChunkName: "fhir-attachment" */ "@/components/fhir/fhir-attachment" ),
    "fhir-reference": () => import(/* webpackChunkName: "fhir-reference" */ "@/components/fhir/fhir-reference" )
  },
  created() {
    this.populateCarry()
  }
}
</script>